import React, { useContext, useState } from "react";
import Modal from "../Modal/Modal";

import { RafflesContext } from "../../Context/RafflesContext";

const Navbar = () => {
  const [show, setShow] = useState(false);

  const { publicKey } = useContext(RafflesContext);

  return (
    <>
      <nav className="px-2 sm:px-4 py-2.5">
        <div className="container flex flex-wrap items-center justify-between mx-auto">
          <div className="flex items-center">
            <span className="self-center text-3xl md:text-4xl SteinerPremium whitespace-nowrap text-[#F9B508]">
              SOVEREIGN
            </span>
          </div>

          <div className="flex md:order-2">
            {publicKey ? (
              <button
                type="button"
                className="bg-[#F9B508] hover:bg-opacity-80 focus:ring-4 focus:outline-none focus:ring-yellow-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-3 md:mr-0 "
              >
                {`${publicKey?.slice(0, 7)} ...${publicKey?.slice(-7)}`}
              </button>
            ) : (
              <button
                onClick={() => setShow(true)}
                type="button"
                className="bg-[#06B036] hover:bg-opacity-80 focus:ring-4 focus:outline-none focus:ring-green-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-3 md:mr-0 "
              >
                Select wallet
              </button>
            )}

            <button
              data-collapse-toggle="navbar-cta"
              type="button"
              className="inline-flex items-center p-2 text-sm rounded-lg md:hidden hover:bg-[#F9B508] focus:outline-none focus:ring-2 focus:ring-[#F9B508]"
              aria-controls="navbar-cta"
              aria-expanded="false"
            >
              <span className="sr-only">Open main menu</span>
              <svg
                className="w-6 h-6"
                aria-hidden="true"
                fill="currentColor"
                viewBox="0 0 20 20"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fillRule="evenodd"
                  d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 15a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z"
                  clipRule="evenodd"
                />
              </svg>
            </button>
          </div>
          <div
            className="items-center justify-between hidden text-white w-full md:flex md:w-auto md:order-1 cursor-pointer"
            id="navbar-cta"
          >
            <ul className="flex flex-col p-4 mt-4 text-white  md:flex-row md:space-x-8 md:mt-0 md:text-base md:font-medium ">
              <li>
                <h4
                  className="block py-2 pl-3 pr-4 md:text-[#DAA10F] md:p-0"
                  aria-current="page"
                >
                  Home
                </h4>
              </li>
              <li>
                <h4 className="block py-2 pl-3 pr-4  md:hover:text-[#DAA10F] md:p-0">
                  Staking
                </h4>
              </li>
              <li>
                <h4 className="block py-2 pl-3 pr-4  md:hover:text-[#DAA10F] md:p-0">
                  Store
                </h4>
              </li>
              <li>
                <h4 className="block py-2 pl-3 pr-4   md:hover:text-[#DAA10F] md:p-0">
                  Whitepaper
                </h4>
              </li>
              <li>
                <h4 className="block py-2 pl-3 pr-4  md:hover:text-[#DAA10F] md:p-0">
                  Upgrade
                </h4>
              </li>
            </ul>
          </div>
        </div>
        <hr className="container mx-auto border-[#D9D9D9] border hidden md:block" />
      </nav>

      {/* Modal  */}
      <div onClick={() => setShow(false)}>{show && <Modal />}</div>
    </>
  );
};

export default Navbar;
