import React, { useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import BaseService from "../../services/BaseService";

import { RafflesContext } from "../../Context/RafflesContext";

const RafflesDetails = () => {
  const [inputValue, setInputValue] = useState(1);
  const [activeTab, setActiveTab] = useState("Details");
  const [rafffleToken, setRafffleToken] = useState([]);
  const [participantsTransaction, setParticipantsTransaction] = useState([]);
  const [raffflesWinner, setRaffflesWinner] = useState({});

  // const buyerWallet = localStorage.getItem("Wallet");
  const { initializeClock, buyRafffleTicket, timeValue } =
    useContext(RafflesContext);

  const {
    collection_name,
    createdAt,
    nft_name,
    raffel_end_date,
    ticket_price,
    ticket_remaining,
    ticket_supply,
    url,
    token_address,
    wallet_address,
  } = rafffleToken;

  //  { buyers_wallet, updatedAt, quantity, txHash }

  const raffflesTabs = [
    { id: "Details", label: "Details" },
    { id: "Transactions", label: "Transactions" },
  ];

  const handleClick = (tabId) => {
    setActiveTab(tabId);
  };

  const { token } = useParams();

  useEffect(() => {
    getRafflesByToken();
    if (ticket_remaining === 0 || timeValue?.total <= 0) {
      const getRafflesWinner = async () => {
        const data = { token_address: token };
        const raffleWinner = await BaseService.post(`getWinner`, data);
        setRaffflesWinner(raffleWinner?.data?.random);
      };
      getRafflesWinner();
    }
    // eslint-disable-next-line
  }, [ticket_remaining, timeValue?.total]);

  const getRafflesByToken = async () => {
    const rafffleToken = setInterval(async () => {
      const data = { token_address: token };
      const res = await BaseService.post(`getDataByToken`, data);
      setRafffleToken(res.data.result);

      // const buyData = { buyers_wallet: buyerWallet, token_address: token };
      const participent = await BaseService.post(`participants`, data);
      setParticipantsTransaction(participent?.data?.result);
    }, 5000);

    return () => clearInterval(rafffleToken);
  };

  // const getRafflesWinner = async () => {
  //   const data = { token_address: token };
  //   const raffleWinner = await BaseService.post(`getWinner`, data);
  //   setRaffflesWinner(raffleWinner?.data?.random);
  //   console.log("🚀 ~ getRafflesWinner ~ raffleWinner", raffleWinner);
  // };

  return (
    <>
      <div className="pt-[75px] min-h-screen">
        <div className="max-w-screen-xl mx-auto md:px-8 md:py-4 flex w-full flex-col md:flex-row">
          <div className="md:w-1/3 md:mr-4 px-4 pt-4 md:pt-0 md:px-0">
            <div className="relative rounded-2xl overflow-hidden mb-4">
              <button className="absolute top-3 right-3 p-2 flex items-center bg-white rounded-lg">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 512 512"
                  className="text-[#FE0505] w-4 fill-current inline-block"
                >
                  <path d="M462.3 62.6C407.5 15.9 326 24.3 275.7 76.2L256 96.5l-19.7-20.3C186.1 24.3 104.5 15.9 49.7 62.6c-62.8 53.6-66.1 149.8-9.9 207.9l193.5 199.8c12.5 12.9 32.8 12.9 45.3 0l193.5-199.8c56.3-58.1 53-154.3-9.8-207.9z" />
                </svg>
              </button>
              <img
                className="w-full"
                src={
                  url ||
                  "https://bafybeibnmqokbtlkxhkhxmmjcnc75w57hc74fj7ucgk4crw4gezm3xccju.ipfs.nftstorage.link/1006.png?ext=png"
                }
                loading="lazy"
                alt=""
              />
            </div>

            {/* Winner Start  */}
            {ticket_remaining === 0 || timeValue?.total <= 0 ? (
              <div className="text-center border-4 border-yellow-500 py-5 rounded-xl">
                <div className="flex flex-col items-center justify-center">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 640 512"
                    className="w-6 mr-3 text-yellow-500 fill-current"
                  >
                    <path d="M528 464H112c-8.8 0-16 7.2-16 16v16c0 8.8 7.2 16 16 16h416c8.8 0 16-7.2 16-16v-16c0-8.8-7.2-16-16-16zm64-336c-26.5 0-48 21.5-48 48 0 7.1 1.6 13.7 4.4 19.8L476 239.2c-5.2 3.1-10.8 4.6-16.4 4.6-11.1 0-21.9-5.8-27.8-16.1L350.3 85C361 76.2 368 63 368 48c0-26.5-21.5-48-48-48s-48 21.5-48 48c0 15 7 28.2 17.7 37l-81.5 142.6c-5.9 10.4-16.7 16.1-27.8 16.1-5.6 0-11.3-1.5-16.4-4.6l-72.3-43.4c2.7-6 4.4-12.7 4.4-19.8 0-26.5-21.5-48-48-48S0 149.5 0 176s21.5 48 48 48c2.6 0 5.2-.4 7.7-.8L128 416h384l72.3-192.8c2.5.4 5.1.8 7.7.8 26.5 0 48-21.5 48-48s-21.5-48-48-48zM478.7 368H161.3l-36-96.1 14 8.4c12.4 7.5 26.7 11.4 41.1 11.4 28.7 0 55.3-15.4 69.5-40.3L320 128.7l70.1 122.7c14.2 24.9 40.8 40.3 69.5 40.3 14.5 0 28.7-3.9 41.1-11.4l14-8.4-36 96.1z" />
                  </svg>
                  <div className="font-bold text-yellow-500 mb-1">
                    RAFFLE WINNER!
                  </div>
                </div>
                <div className="text-xs  text-white font-bold w-full truncate px-3">
                  {raffflesWinner?.buyers_wallet}
                </div>
                <strong className="block text-yellow-500 mt-2">
                  Won with {raffflesWinner?.quantity} Ticket(s)
                </strong>
              </div>
            ) : (
              <>
                <div className="flex">
                  <div className="mr-2 lg:mr-6 w-[100px] lg:w-[150px]">
                    <input
                      className="border-2 w-full h-full text-center border-white bg-[#ECE4D0] dark:border-primary focus:border-[#DAA10F] appearance-none  focus:outline-none p-3 bg-offbase rounded-2xl text-black font-bold text-2xl"
                      type="number"
                      value={inputValue}
                      onChange={(event) => setInputValue(event.target.value)}
                    />
                  </div>
                  <div className="flex w-full">
                    <button
                      onClick={() =>
                        buyRafffleTicket(
                          wallet_address,
                          ticket_price,
                          inputValue,
                          token_address
                        )
                      }
                      className="w-full py-5 lg:py-0 bg-gradient-to-t from-[#DAA10F] to-[#DAA10F] justify-center items-center opacity-90 hover:opacity-100 rounded-xl text-white text-xl font-bold transition"
                    >
                      Buy Raffle Ticket
                    </button>
                  </div>
                </div>
                <div className="flex mt-3">
                  <div className="lg:mr-6 lg:w-[150px]" />
                  <div className="w-full text-center">
                    <div className="text-white text-xl font-bold text-center">
                      {ticket_price || 0.17} SOL / Ticket
                    </div>
                  </div>
                </div>
              </>
            )}
            {/* Winner End  */}
          </div>
          <div className="md:w-2/3 bg-[#2F2A2A]  w-full md:rounded-2xl p-8 mt-5 md:mt-0 flex flex-col justify-between transition">
            <div className="w-full">
              <div className="flex flex-col md:flex-row flex-between">
                <div className="w-full">
                  <div className="flex items-center capitalize">
                    <span className="capitalize">
                      {collection_name || "lily"}
                    </span>
                  </div>
                  <div>
                    <strong className="text-4xl pb-1 ">
                      {nft_name || "LILY #4465"}
                    </strong>
                  </div>
                </div>
              </div>
              <div className="flex gap-x-2 mt-2 " />
              <div className="flex justify-between items-center pt-3 mb-3 ">
                <div>
                  {raffflesTabs?.map((item) => (
                    <button
                      className={`${
                        item?.id === activeTab.toString() && "bg-[#DAA10F]"
                      }  text-white font-bold px-4 py-2 rounded-lg transition`}
                      key={item.id}
                      onClick={() => handleClick(item.id)}
                    >
                      {item?.label}
                    </button>
                  ))}
                </div>
              </div>
              <div className="py-5 border-t border-b border-gray-200 dark:border-primary">
                {activeTab === "Details" && (
                  <div className="grid grid-cols-1 md:grid-cols-2 font-bold gap-3 gap-y-6">
                    <div>
                      <strong className="block text-sm  ">
                        Ticket Sales Ends in:
                      </strong>
                      {raffel_end_date && (
                        <div className="text-xl ">
                          <div>
                            <section>
                              <div
                                id="counter"
                                onLoad={initializeClock(raffel_end_date)}
                              >
                                <span className="di" />
                                <span className="ho" />
                                <span className="mi" />
                                <span className="se" />
                                <div></div>
                              </div>
                            </section>
                          </div>
                        </div>
                      )}
                    </div>
                    <div className="flex">
                      <div className>
                        <strong className="block text-sm  ">
                          Ticket Cost:
                        </strong>
                        <div className="text-xl relative ">
                          <div className="flex items-center gap-2">
                            {ticket_price || 0.17} SOL
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className>
                      <strong className="block text-sm  ">
                        Raffle Start Date:
                      </strong>
                      <div className="text-xl ">
                        {new Date(createdAt).toLocaleDateString("en-US", {
                          year: "numeric",
                          month: "short",
                          day: "numeric",
                        })}
                      </div>
                    </div>
                    <div className>
                      <strong className="block text-sm  ">
                        Tickets Remaining:
                      </strong>
                      <div className="  text-xl">
                        <div className="flex items-center gap-2">
                          {ticket_remaining} / {ticket_supply}
                        </div>
                      </div>
                    </div>
                  </div>
                )}

                {activeTab === "Transactions" && (
                  <div className="-mb-1">
                    <div
                      className="ScrollbarsCustom trackYVisible"
                      style={{
                        position: "relative",
                        width: "100%",
                        height: "400px",
                      }}
                    >
                      <div
                        className="ScrollbarsCustom-Wrapper"
                        style={{
                          position: "absolute",
                          inset: "0px 10px 0px 0px",
                          overflow: "hidden",
                        }}
                      >
                        <div
                          className="ScrollbarsCustom-Scroller"
                          style={{
                            position: "absolute",
                            inset: "0px",
                            overflow: "hidden scroll",
                            "margin-right": "-17px",
                          }}
                        >
                          <div
                            className="ScrollbarsCustom-Content"
                            style={{
                              "box-sizing": "border-box",
                              padding: "0.05px",
                              "min-height": "100%",
                              "min-width": "100%",
                            }}
                          >
                            <table className="w-full dark:text-white">
                              <thead>
                                <tr>
                                  <th className="text-left px-3">Txn</th>
                                  <th className="text-left px-3">Buyer</th>
                                  <th className="text-left">Date</th>
                                  <th className="text-center pr-8">Tickets</th>
                                </tr>
                              </thead>
                              <tbody>
                                {participantsTransaction?.map((i, index) => (
                                  <tr
                                    className={
                                      index % 2 === 0 ? "bg-[#413E3E]" : ""
                                    }
                                  >
                                    <td className="text-left py-2">
                                      <div className="hover:text-[#DAA10F] ">
                                        {`${i?.txHash.slice(
                                          0,
                                          4
                                        )} ... ${i?.txHash.slice(-4)}`}
                                      </div>
                                    </td>
                                    <td className="text-left py-2">
                                      <div className="hover:text-[#DAA10F] ">
                                        {`${i?.buyers_wallet.slice(
                                          0,
                                          4
                                        )} ... ${i?.buyers_wallet.slice(-4)}`}
                                      </div>
                                    </td>
                                    <td className="hover:text-[#DAA10F]">
                                      {new Date(
                                        i?.updatedAt
                                      ).toLocaleDateString("en-US", {
                                        year: "numeric",
                                        month: "short",
                                        day: "numeric",
                                      })}
                                    </td>
                                    <td className="text-center py-2 pr- hover:text-[#DAA10F]">
                                      {i?.quantity}
                                    </td>
                                  </tr>
                                ))}
                              </tbody>
                            </table>
                          </div>
                        </div>
                      </div>
                      <div
                        className="ScrollbarsCustom-Track ScrollbarsCustom-TrackY"
                        style={{
                          position: "absolute",
                          overflow: "hidden",
                          "border-radius": "4px",
                          background: "rgba(0, 0, 0, 0.1)",
                          "-webkit-user-select": "none",
                          "-moz-user-select": "none",
                          "-ms-user-select": "none",
                          "user-select": "none",
                          width: "10px",
                          height: "calc(100% - 20px)",
                          top: "10px",
                          right: "0px",
                        }}
                      >
                        <div
                          className="ScrollbarsCustom-Thumb ScrollbarsCustom-ThumbY"
                          style={{
                            cursor: "pointer",
                            "border-radius": "4px",
                            background: "rgba(0, 0, 0, 0.4)",
                            width: "100%",
                            height: "300.395px",
                            "-webkit-transform": "translateY(0px)",
                            "-ms-transform": "translateY(0px)",
                            transform: "translateY(0px)",
                          }}
                        />
                      </div>
                      <div
                        className="ScrollbarsCustom-Track ScrollbarsCustom-TrackX"
                        style={{
                          position: "absolute",
                          overflow: "hidden",
                          "border-radius": "4px",
                          background: "rgba(0, 0, 0, 0.1)",
                          "-webkit-user-select": "none",
                          "-moz-user-select": "none",
                          "-ms-user-select": "none",
                          "user-select": "none",
                          height: "10px",
                          width: "calc(100% - 20px)",
                          bottom: "0px",
                          left: "10px",
                          display: "none",
                        }}
                      >
                        <div
                          className="ScrollbarsCustom-Thumb ScrollbarsCustom-ThumbX"
                          style={{
                            cursor: "pointer",
                            "border-radius": "4px",
                            background: "rgba(0, 0, 0, 0.4)",
                            height: "100%",
                            width: "0px",
                            display: "none",
                          }}
                        />
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </div>
            <div className="p-5 border border-purple-200 rounded-xl   mt-4">
              <h3 className="font-bold text-[#DAA10F]">
                Terms &amp; Conditions
              </h3>
              <ol className="list-decimal ml-5 ">
                <li>
                  Here's a{" "}
                  <span className="text-[#DAA10F] cursor-pointer">guide </span>
                  to buy into raffles.
                </li>
                <li>
                  All NFT prizes are held by rafffle in escrow and can be
                  claimed by the winner or creator once the draw is done.
                </li>
                <li>Raffle tickets cannot be refunded once bought.</li>
                <li>
                  Raffle tickets will not be refunded if you did not win the
                  raffle.
                </li>
                <li>You can only buy 20% of total tickets.</li>
                <li>You'll be charged 1% fees for swapping through Jupiter.</li>
                <li>
                  FFF receives a portion of the fees generated for anyone
                  utilizing the Raven services through our website.
                </li>
              </ol>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default RafflesDetails;
