import React from "react";

const Footer = () => {
  return (
    <>
      <footer className="text-center text-white my-10">
        <div className="container mx-auto pt-6">
          <div className="flex flex-col md:flex-row gap-2 md:gap-0 justify-between items-center mb-6">
            <div className="text-center">
              &copy; {new Date().getFullYear()} - ALL RIGHTS RESERVED.
            </div>
            <div className="flex items-center gap-2 text-black">
              <button className="bg-[#9C7730]  transform hover:-translate-y-3  w-10 h-10 rounded-full duration-500   hover:bg-blue-400 hover:text-white text-xl">
                <i className="fab fa-twitter"></i>
              </button>

              <button className="bg-[#9C7730]  transform hover:-translate-y-3  w-10 h-10 rounded-full duration-500   hover:bg-indigo-500 hover:text-white text-xl">
                <i className="fab fa-discord"></i>
              </button>
            </div>
          </div>
          <div className="mb-6"></div>
          <div className="text-center text-[#252222] SteinerPremium text-7xl xl:text-9xl">
            <h1>SOVEREIGN</h1>
          </div>
        </div>
      </footer>
    </>
  );
};

export default Footer;
