import React, { useState, createContext } from "react";
import { toast } from "react-toastify";
import BaseService from "../services/BaseService";
// Solana
import * as solanaWeb3 from "@solana/web3.js";

export const RafflesContext = createContext();

export const RafflesProvider = ({ children }) => {
  const [publicKey, setPublicKey] = useState("");
  const [rafflesData, setRafflesData] = useState([]);

  const [timeValue, setTimeValue] = useState([]);

  //   Solana Wallet Connect
  const connectWallet = async () => {
    if (window?.solana) {
      const solana = window?.solana;
      const res = await solana?.connect();
      setPublicKey(res?.publicKey?.toString());
      localStorage.setItem("Wallet", res?.publicKey?.toString());
    } else {
      window.open("https://phantom.app/");
    }
  };

  // *********  the Timer function:  *********
  function getRemainingTime(endTime) {
    const total = Date.parse(endTime) - Date.parse(new Date());
    const seconds = Math.floor((total / 1000) % 60);
    const minutes = Math.floor((total / 1000 / 60) % 60);
    const hours = Math.floor((total / (1000 * 60 * 60)) % 24);
    const days = Math.floor(total / (1000 * 60 * 60 * 24));

    return {
      total,
      days,
      hours,
      minutes,
      seconds,
    };
  }

  const initializeClock = (endTime) => {
    const updateClock = () => {
      let sections = document.querySelectorAll("section");

      let t = getRemainingTime(endTime);
      setTimeValue(t);

      for (let i = 0; i < sections.length; i++) {
        let daysSpan = document.getElementsByClassName("di");
        let hoursSpan = document.getElementsByClassName("ho");
        let minutesSpan = document.getElementsByClassName("mi");
        let secondsSpan = document.getElementsByClassName("se");

        daysSpan[i].innerHTML = ("0" + t.days).slice(-2) + " day ";
        hoursSpan[i].innerHTML = ("0" + t.hours).slice(-2) + " hr ";
        minutesSpan[i].innerHTML = ("0" + t.minutes).slice(-2) + " min ";
        secondsSpan[i].innerHTML = ("0" + t.seconds).slice(-2) + " s ";
      }

      if (t.total <= 0) {
        clearInterval(timeinterval);
        document.getElementById("counter").innerHTML = "The countdown is over!";
      }
    };
    const timeinterval = setInterval(updateClock, 1000);
  };

  // *********  the end of the Timer function:  *********

  // Get Raffles Data
  const getRafflesData = async () => {
    const res = await BaseService.get(`getAllData`);
    return setRafflesData(res.data.result);
  };

  // Timer Function
  const rafflesTimer = (endTime) => {
    var distance;
    // Update the count down every 1 second
    var x = setInterval(function () {
      // Set the date we're counting down to
      var countDownDate = new Date(endTime).getTime();
      // Get today's date and time
      var now = new Date().getTime();
      // Find the distance between now and the count down date
      distance = countDownDate - now;
      // Time calculations for days, hours, minutes and seconds
      var days = Math.floor(distance / (1000 * 60 * 60 * 24));
      var hours = Math.floor(
        (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
      );
      var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      var seconds = Math.floor((distance % (1000 * 60)) / 1000);
      // Output the result in an element with id="demo"
      document.getElementById("demo").innerHTML =
        " " + days + "d " + hours + "h " + minutes + "m " + seconds + "s ";
    }, 1000);
    // If the count down is over, write some text
    if (distance < 0) {
      clearInterval(x);
      document.getElementById("demo").innerHTML = "EXPIRED";
    }
  };

  // Connection to devnet.
  const connection = new solanaWeb3.Connection(
    solanaWeb3.clusterApiUrl("devnet"),
    "confirmed"
  );

  //  ******* the buyrafflesticket *******
  const buyRafffleTicket = async (
    wallet_address,
    ticket_price,
    inputValue,
    token_address
  ) => {
    if ((!wallet_address, !ticket_price, !inputValue, !token_address))
      return toast.error("Something went wrong..!");
    // Solana connect
    const solana = window?.solana;
    const res = await solana?.connect();
    // Convert to object type.
    const fromWallet = new solanaWeb3.PublicKey(res?.publicKey?.toString());
    const toWallet = new solanaWeb3.PublicKey(wallet_address);

    // Add transfer instructions to transaction.
    const transaction = new solanaWeb3.Transaction().add(
      solanaWeb3.SystemProgram.transfer({
        fromPubkey: fromWallet,
        toPubkey: toWallet,
        lamports: ticket_price * inputValue * 1000000000, // 1 Lamport = 10^-9 SOL.
      })
    );
    // Setting the variables for the transaction.
    transaction.feePayer = await window?.solana?.publicKey;
    transaction.recentBlockhash = (
      await connection.getLatestBlockhash()
    ).blockhash;
    // Request creator to sign the transaction.
    const signed = await window.solana.signTransaction(transaction);
    // Signature generated.
    const signature = await connection.sendRawTransaction(signed.serialize());
    // Confirm whether the transaction went through or not.
    await connection.confirmTransaction(signature);
    console.log("🚀 ~ sendSOL ~ signature", signature);

    const bodyData = {
      buyers_wallet: res?.publicKey?.toString(),
      txHash: signature,
      token_address: token_address,
      quantity: inputValue,
    };
    BaseService.post(`buyTicket`, bodyData)
      .then((response) => {
        // handle success
        console.warn(response);
        toast.success(response?.data?.message);
      })
      .catch((err) => {
        // handle error
        console.log(err);
      });
  };

  //  ******* the end buyrafflesticket  *******

  return (
    <RafflesContext.Provider
      value={{
        publicKey,
        rafflesData,
        timeValue,
        connectWallet,
        getRafflesData,
        rafflesTimer,
        initializeClock,
        buyRafffleTicket,
      }}
    >
      {children}
    </RafflesContext.Provider>
  );
};
