import React, { useEffect, useState } from "react";
import BaseService from "../../services/BaseService";
import RafflesItems from "../RafflesItems/RafflesItems";

const Home = () => {
  const [rafflesData, setRafflesData] = useState([]);

  useEffect(() => {
    getRaffles();
  }, []);

  const getRaffles = async () => {
    const res = await BaseService.get(`getAllData`);
    await setRafflesData(res.data.result);
  };
  // grid md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4
  return (
    <>
      <div className="flex items-center flex-wrap justify-center gap-4">
        {rafflesData?.map((item, index) => (
          <div key={index}>
            <RafflesItems item={item} />
          </div>
        ))}
      </div>
    </>
  );
};

export default Home;
